import { CommonModule } from '@angular/common';
import { Component, ElementRef, forwardRef, Inject, Injector, NgModule, Optional, ViewEncapsulation } from '@angular/core';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material';

import { OSharedModule } from '../../../shared/shared.module';
import { OFormComponent } from '../../form/form-components';
import { OContainerCollapsibleComponent } from '../o-container-collapsible-component.class';

export const DEFAULT_INPUTS_O_ROW_COLLAPSIBLE = [
  ...OContainerCollapsibleComponent.DEFAULT_INPUTS_O_CONTAINER_COLLAPSIBLE
];

@Component({
  moduleId: module.id,
  selector: 'o-row-collapsible',
  template: `
    <mat-expansion-panel #expPanel [expanded]="expanded" class="o-container">
      <mat-expansion-panel-header #containerTitle [expandedHeight]="expandedHeight" [collapsedHeight]="collapsedHeight">
        <mat-panel-title fxLayout="row" fxLayoutAlign="start center" class="o-container-title">
          <mat-icon *ngIf="icon">{{ icon }}</mat-icon>
          <span *ngIf="title">{{ title | oTranslate }}</span>
        </mat-panel-title>
        <mat-panel-description fxLayout="row" fxLayoutAlign="start center">
          <span>{{ description | oTranslate }}</span>
        </mat-panel-description>
      </mat-expansion-panel-header>
      <div #containerContent id="innerRow" fxLayout="row" fxLayoutAlign="{{ layoutAlign }}" fxLayoutGap="{{ layoutGap }}"
        class="o-container-scroll o-scroll" fxFlex="grow">
        <ng-content></ng-content>
      </div>
    </mat-expansion-panel>
    <div #container [class.o-container-outline-expanded]="expPanel.expanded" class="o-container-outline"
      *ngIf="isAppearanceOutline()">
      <div class="o-container-outline-start"></div>
      <div class="o-container-outline-gap-title"></div>
      <div class="o-container-outline-gap-empty1"></div>
      <div class="o-container-outline-gap-description"></div>
      <div class="o-container-outline-gap-empty2"></div>
      <div class="o-container-outline-gap-icon"></div>
      <div class="o-container-outline-end"></div>
    </div>
  `,
  styles: [`

  `],
  inputs: DEFAULT_INPUTS_O_ROW_COLLAPSIBLE,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-row-collapsible]': 'true',
    '[class.o-appearance-outline]': 'isAppearanceOutline()',
    '[class.o-appearance-outline-title]': 'hasTitleInAppearanceOutline()'
  }
})
export class ORowCollapsibleComponent extends OContainerCollapsibleComponent {

  public static DEFAULT_INPUTS_O_ROW_COLLAPSIBLE = DEFAULT_INPUTS_O_ROW_COLLAPSIBLE;

  constructor(
    @Optional() @Inject(forwardRef(() => OFormComponent)) protected form: OFormComponent,
    protected elRef: ElementRef,
    protected injector: Injector,
    @Optional() @Inject(MAT_FORM_FIELD_DEFAULT_OPTIONS) protected matFormDefaultOption
  ) {
    super(form, elRef, injector, matFormDefaultOption);
  }

}

@NgModule({
  declarations: [ORowCollapsibleComponent],
  imports: [CommonModule, OSharedModule],
  exports: [ORowCollapsibleComponent]
})
export class ORowCollapsibleModule { }
